.container {
  height: 60px;
  background-color: #656fcc;
  justify-content: center;
  align-items: center;
  display: flex;
}

body {
  color: #232946;
  background-color: #d4d8f0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

input, button {
  color: #232946;
}

img {
  width: 100%;
  object-fit: cover;
  height: 250px;
  display: block;
}

input {
  box-sizing: border-box;
  background-color: #d4d8f0;
  border: 1px solid rgba(35, 41, 70, .1);
  border-radius: .25rem;
  justify-content: center;
  margin: 0;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  box-shadow: 0 1px 3px rgba(35, 41, 70, .02);
}

input:hover, input:focus {
  color: rgba(35, 41, 70, .65);
  border-color: rgba(35, 41, 70, .15);
  box-shadow: 0 4px 12px rgba(35, 41, 70, .1);
}

input:hover {
  transform: translateY(-1px);
}

input:active {
  color: rgba(35, 41, 70, .65);
  border-color: rgba(35, 41, 70, .15);
  transform: translateY(0);
  box-shadow: 0 2px 4px rgba(35, 41, 70, .06);
}

button {
  box-sizing: border-box;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
  background-color: #eebbc3;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: .25rem;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  text-decoration: none;
  transition: all .25s;
  display: inline-flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .02);
}

button[disabled] {
  color: #f0f0f1;
  pointer-events: none;
  background-color: #898b9b;
}

button:hover, button:focus {
  color: rgba(0, 0, 0, .65);
  border-color: rgba(0, 0, 0, .15);
  box-shadow: 0 4px 12px rgba(0, 0, 0, .1);
}

button:hover {
  transform: translateY(-1px);
}

button:active {
  color: rgba(0, 0, 0, .65);
  background-color: #f0f0f1;
  border-color: rgba(0, 0, 0, .15);
  transform: translateY(0);
  box-shadow: 0 2px 4px rgba(0, 0, 0, .06);
}

.photo-card {
  width: calc(25% - 11.25px);
  background: #fff;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.info {
  text-align: center;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  margin: 15px auto;
  padding: 0 15px;
  display: flex;
}

.load-more {
  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.load-more.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

